import React, { useState, useEffect } from 'react';
import Calendar from 'react-calendar'; // Import react-calendar
import 'react-calendar/dist/Calendar.css'; // Import the CSS for react-calendar
import './AttendanceCalendar.css';
import { db } from '../firebase'; // Import Firebase configuration
import { doc, getDoc } from "firebase/firestore"; // Import Firestore functions

const AttendanceCalendar = ({ setSelectedDate, attendance, updateAttendance }) => {
    const [selectedDate, setSelectedDateLocal] = useState(new Date());
    const [isHolidayOrSunday, setIsHolidayOrSunday] = useState(false);
    const [isPastDate, setIsPastDate] = useState(false);
    const [canUpdateMaybe, setCanUpdateMaybe] = useState(false);
    const [holidays, setHolidays] = useState([]);
    const [holidayDescription, setHolidayDescription] = useState("");
    const [isCurrentMonth, setIsCurrentMonth] = useState(true);

    // Function to fetch holidays for the selected month and year       
    
    let mn = selectedDate.getMonth() + 1;
        let dy = selectedDate.getDate();
        let yr = selectedDate.getFullYear();
        let formattedSelectDate = `${dy < 10 ? "0" + dy : dy}-${mn < 10 ? "0" + mn : mn}-${yr}`;

    const fetchHolidays = async (year, month) => {
        try {
            const monthDoc = `${year}-${month.toLowerCase()}`; // Format month name
            const docRef = doc(db, 'holidays', monthDoc);
            const docSnap = await getDoc(docRef);

            if (docSnap.exists()) {
                setHolidays(Object.keys(docSnap.data()).map(date => ({
                    date,
                    description: docSnap.data()[date]
                })));
            } else {
                console.log("No holidays found for the selected month and year");
                setHolidays([]); // Clear holidays if not found
            }
        } catch (error) {
            console.error('Error fetching holidays: ', error);
        }
    };

    // Fetch holidays for the current year and month when the component mounts
    useEffect(() => {
        const today = new Date();
        const year = today.getFullYear();
        const month = today.toLocaleString('default', { month: 'long' }); // Get the current month name

        fetchHolidays(year, month);
    }, []);

    const isHoliday = (date) => {
        let mn = date.getMonth() + 1;
        let dy = date.getDate();
        let yr = date.getFullYear();
        let formattedDate = `${yr}-${mn < 10 ? "0" + mn : mn}-${dy < 10 ? "0" + dy : dy}`;
        // console.log(formattedDate)
        
        const holiday = holidays.find(holiday => holiday.date === formattedDate);
        
        if (holiday) {
           
          
            setHolidayDescription(holiday.description);
           
            return true;
        }else{
        // setHolidayDescription(null); // Clear description if not a holiday
        // return false;       
        }
    };

    const isSunday = (date) => {
        return date.getDay() === 0 ; // 0 is Sunday
       
    }
   
   
    const getAttendanceStatus = (date) => {
        let mn = date.getMonth() + 1;
        let dy = date.getDate();
        let yr = date.getFullYear();
        let formattedDate = `${yr}-${mn < 10 ? "0" + mn : mn}-${dy < 10 ? "0" + dy : dy}`;
        return attendance[formattedDate] || null;
    };

    const handleDateChange = (date) => {
        let mn = date.getMonth() + 1;
        let dy = date.getDate();
        let yr = date.getFullYear();
        let formattedDate = `${yr}-${mn < 10 ? "0" + mn : mn}-${dy < 10 ? "0" + dy : dy}`;

        setSelectedDateLocal(date);
        setSelectedDate(formattedDate); // Update parent with the selected date

        const status = getAttendanceStatus(date);

        if (isHoliday(date)) {
            setIsHolidayOrSunday(true);
        } else {
            setIsHolidayOrSunday(false);
        }

        const today = new Date();
        today.setHours(0, 0, 0, 0);
        if (date < today) {
            setIsPastDate(true);
            if (status === 'maybe') {
                setCanUpdateMaybe(true);
            } else {
                setCanUpdateMaybe(false);
            }
        } else {
            setIsPastDate(false);
            setCanUpdateMaybe(false);
        }

        const currentMonth = today.getMonth();
        if (date.getMonth() !== currentMonth) {
            setIsCurrentMonth(false);
        } else {
            setIsCurrentMonth(true);
        }
    };

    const tileClassName = ({ date, view }) => {
        if (view === 'month') {
            if (isHoliday(date)) return 'holiday';
            if (isSunday(date)) return 'sunday';
            const status = getAttendanceStatus(date);
            if (status) {
                if (status === 'present') {
                    return 'tile-present';
                } else if (status === 'absent') {
                    return 'tile-absent';
                } else if (status === 'maybe') {
                    return 'tile-maybe';
                }
            }
        }
        return null;
    };

    return (
        <div className="attendance-calendar">
            <Calendar
                onChange={handleDateChange}
                value={selectedDate}
                tileClassName={tileClassName}
            />

            
            {/* dashboard  */}
            <div className='dashboard'>
            <div className="dashboard-card ">
                  {/* pichart */}
                    <img src="https://cdn-icons-png.flaticon.com/512/3616/3616838.png" width={"50px"} alt="" />
                </div>
                <div className="dashboard-card ">
                   <h3 className='present-h3'>75%</h3>
                    <h6>20Days</h6>
                </div>


                <div className="dashboard-card ">
                   <h3 className='absent-h3'>10%</h3>
                    <h6>20Days</h6>
                </div>


                <div className="dashboard-card ">
                   <h3 className='holiday-h3'>40%</h3>
                    <h6>20Days</h6>
                </div>

            </div>


            {/* buttons  */}
            {!isHolidayOrSunday && isCurrentMonth && (!isPastDate || canUpdateMaybe) && !isSunday(selectedDate) && (
                <div className="attendance-options">
                    <button className="present-btn" onClick={() => updateAttendance('present')}>Present</button>
                    <button className="absent-btn" onClick={() => updateAttendance('absent')}>Absent</button>
                    <button className="maybe-btn" onClick={() => updateAttendance('maybe')}>Maybe</button>
                </div>
            )}
            {isSunday(selectedDate) && !isPastDate && isCurrentMonth &&(
                <div className='alert-box'>
                <p className="holiday-message">This day is Sunday.</p>
                </div>
            )}
            {isHolidayOrSunday && holidayDescription  && (
                <div className='alert-box'>
                <p className="holiday-message">This day is Holiday on {formattedSelectDate}.</p>
                </div>
            )}

            {isPastDate && !canUpdateMaybe && (
                <div className='alert-box'>
                <p className="past-date-message">You can't change the past</p>
                </div>
            )}

            {!isCurrentMonth && !isPastDate &&(
                <div className='alert-box'>
                <p className="month-restriction-message">You can't change the future month because is Dark.</p>
                </div>
            )}

            <div className="attendance-summary">
                <h3>Attendance List:</h3>
                {attendance && Object.keys(attendance).length > 0 ? (
                    <ul>
                        {Object.keys(attendance).map((user) => (
                            <React.Fragment key={user}>
                                <li>
                                    {user} <div className='Status'>{attendance[user]} <span className={`dot ${attendance[user]}`}></span></div>
                                </li>
                                <hr />
                            </React.Fragment>
                        ))}
                    </ul>
                ) : (
                    <p>No attendance recorded for this date.</p>
                )}
            </div>
        </div>
    );
};

export default AttendanceCalendar;
