import React, { useState, useEffect } from 'react';
import { collection, getDocs, doc, getDoc, setDoc } from 'firebase/firestore'; // Add these imports
import { db, auth } from '../firebase'; // Assuming you already have these
import AttendanceCalendar from './AttendanceCalendar';
import { onAuthStateChanged, signOut } from 'firebase/auth';

const Home = () => {
    const [attendance, setAttendance] = useState({});
    const [user, setUser] = useState(null);
    const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
    const [holidays, setHolidays] = useState([]);

    useEffect(() => {
        const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
            setUser(currentUser);
        });
        return () => unsubscribe();
    }, []);

    useEffect(() => {
        const fetchAttendance = async () => {
            if (!user || holidays.includes(selectedDate)) return;

            const docRef = doc(db, 'attendance', selectedDate); // Fetching document for the selected date
            const docSnap = await getDoc(docRef);

            if (docSnap.exists()) {
                setAttendance(docSnap.data());
            } else {
                setAttendance({});
            }
        };

        fetchAttendance();
    }, [user, selectedDate, holidays]);

    useEffect(() => {
        const fetchHolidays = async () => {
            try {
                const holidaysRef = collection(db, 'holidays'); // Fetching holidays collection
                const querySnapshot = await getDocs(holidaysRef);
                const fetchedHolidays = querySnapshot.docs.map((doc) => doc.id);
                setHolidays(fetchedHolidays);
            } catch (error) {
                console.error("Error fetching holidays: ", error);
            }
        };

        fetchHolidays();
    }, []);

    const updateAttendance = async (status) => {
        if (!user || holidays.includes(selectedDate)) return;

        const attendanceData = { ...attendance, [user.displayName || user.email]: status };

        // Updating attendance for the selected date in Firestore
        await setDoc(doc(db, 'attendance', selectedDate), attendanceData);
        setAttendance(attendanceData);
    };
    const handleLogout = async () => {
        try {
            await signOut(auth); // Sign out the user
            setUser(null); // Update user state to null
        } catch (error) {
            console.error("Error signing out:", error);
            alert(error.message); // Show error message
        }
    };

    return (
        <div className="home">
            <h1>Attendance  {auth.currentUser && (
                <button onClick={handleLogout} className="logout-button">
                    Logout
                </button>
            )} </h1>
            <AttendanceCalendar 
    setSelectedDate={setSelectedDate} 
    attendance={attendance} 
    holidays={holidays} 
    updateAttendance={updateAttendance} // Pass updateAttendance function
/>

            
        </div>
    );
};

export default Home;
